<template>
  <FormsContainer class="container">
    <VTypography class="mb-5" font-weight="heavy" font-style="header-1">{{
      getLocalize.title
    }}</VTypography>

    <div class="row mb-2">
      <div class="col">
        <TextInput
          :label="getLocalize.label"
          :disabled="!editable"
          :placeholder="getLocalize.russianInputPlaceholder"
          v-model="russianInformation"
        >
          <template v-slot:popover>
            <PopoverContent :width="266">
              <VTypography font-weight="medium">{{
                getLocalize.popoverText
              }}</VTypography>
            </PopoverContent>
          </template>
        </TextInput>
      </div>
      <div class="col">
        <TextInput
          v-model="englishInformation"
          root-class="mt-4"
          :disabled="!editable"
          :placeholder="getLocalize.russianInputPlaceholder"
        />
      </div>
    </div>

    <div class="row mb-2">
      <div class="col">
        <TextInput
          type="email"
          v-model="email"
          :multiline="false"
          placeholder="Email"
          :disabled="!editable"
        />
      </div>
    </div>

    <div class="row" v-if="isTechForm">
      <div class="col">
        <TextInput
          label="Технология"
          :disabled="!editable"
          v-model="russianTechnology"
          :placeholder="getLocalize.russianInputPlaceholder"
        >
          <template v-slot:popover>
            <PopoverContent :width="266">
              <VTypography font-weight="medium"
                >Полное наименование Технологии</VTypography
              >
            </PopoverContent>
          </template>
        </TextInput>
      </div>
      <div class="col">
        <TextInput
          root-class="mt-4"
          :disabled="!editable"
          v-model="englishTechnology"
          :placeholder="getLocalize.englishInputPlaceholder"
        />
      </div>
    </div>

    <div class="row" v-if="!isTechForm">
      <div class="col">
        <TextInput
          label="Программа для ЭВМ"
          :disabled="!editable"
          v-model="russianProgram"
          placeholder="Русский текст"
        >
          <template v-slot:popover>
            <PopoverContent :width="320">
              <VTypography font-weight="medium"
                >Полное наименование Программы для ЭВМ</VTypography
              >
            </PopoverContent>
          </template>
        </TextInput>
      </div>
      <div class="col">
        <TextInput
          root-class="mt-4"
          :disabled="!editable"
          v-model="englishProgram"
          placeholder="Английский текст"
        />
      </div>
    </div>
  </FormsContainer>
</template>

<script>
import { mapGetters, mapMutations } from "vuex";

import TextInput from "@/components/ui/TextInput";
import VTypography from "@/components/ui/Typography";
import { isEditableStatus } from "@/helpers/StatusHelpers";
import FormsContainer from "@/components/forms/FormsContainer";
import PopoverContent from "@/components/popover/PopoverContent";
import { FormTypes } from "@/constants/FormConstants";

export default {
  name: "GeneralForm",
  components: {
    TextInput,
    VTypography,
    PopoverContent,
    FormsContainer,
  },
  methods: {
    ...mapMutations("general", [
      "changeEmail",
      "changeRussianProgram",
      "changeEnglishProgram",
      "changeRussianTechnology",
      "changeEnglishTechnology",
      "changeRussianInformation",
      "changeEnglishInformation",
    ]),
  },
  computed: {
    ...mapGetters("formInfo", ["getStatus", "getFormType"]),
    ...mapGetters("general", [
      "getEmail",
      "getLocalize",
      "getRussianProgram",
      "getEnglishProgram",
      "getRussianTechnology",
      "getEnglishTechnology",
      "getEnglishInformation",
      "getRussianInformation",
    ]),
    isTechForm() {
      return this.getFormType === FormTypes.Tech;
    },
    editable() {
      return isEditableStatus(this.getStatus);
    },
    russianInformation: {
      get() {
        return this.getRussianInformation;
      },
      set(value) {
        this.changeRussianInformation(value);
      },
    },
    englishInformation: {
      get() {
        return this.getEnglishInformation;
      },
      set(value) {
        this.changeEnglishInformation(value);
      },
    },
    email: {
      get() {
        return this.getEmail;
      },
      set(value) {
        this.changeEmail(value);
      },
    },
    russianTechnology: {
      get() {
        return this.getRussianTechnology;
      },
      set(value) {
        this.changeRussianTechnology(value);
      },
    },
    englishTechnology: {
      get() {
        return this.getEnglishTechnology;
      },
      set(value) {
        this.changeEnglishTechnology(value);
      },
    },
    russianProgram: {
      get() {
        return this.getRussianProgram;
      },
      set(value) {
        this.changeRussianProgram(value);
      },
    },
    englishProgram: {
      get() {
        return this.getEnglishProgram;
      },
      set(value) {
        this.changeEnglishProgram(value);
      },
    },
  },
};
</script>
